.input:focus-within {
    outline: none;
    transition: box-shadow 150ms ease;
    box-shadow: 0 0 0 2px var(--mantine-primary-color-filled);
  }
  .input[data-variant="unstyled"] {
    outline: none;
    box-shadow: 0 0 0 0 !important;
  }

.noInputRing {
  outline: none;
  box-shadow: 0 0 0 0 !important;
}

.numberInput:focus-within {
    z-index: 1;
  }

.menuDropdown {
  border-radius: var(--mantine-radius-sm);
  min-width: 120px;
}

.menuItem {
  border-radius: var(--mantine-radius-sm);
  padding: 4px var(--mantine-spacing-xs);
}

.actionIcon[data-variant="natural"], .button[data-variant="natural"] {
    background-color: transparent;
    color: var(--mantine-color-gray-6) !important;
  }

.actionIcon[data-variant="natural"]:hover, .button[data-variant="natural"]:hover {
      background-color: var(--mantine-color-gray-2);
    }

[data-mantine-color-scheme='dark'] .actionIcon[data-variant="natural"], [data-mantine-color-scheme='dark'] .button[data-variant="natural"] {
      background-color: transparent;
      color: var(--mantine-color-dark-3) !important;
}

[data-mantine-color-scheme='dark'] .actionIcon[data-variant="natural"]:hover, [data-mantine-color-scheme='dark'] .button[data-variant="natural"]:hover {
        background-color: var(--mantine-color-dark-6);
      }
